.listItem{
  display: flex;
  flex-direction: row;
}
.listItemBtn{
  width: 60px;
  height: 25px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  outline: none;
  border: none;
}
.listItemBtn:active{
  color: aliceblue;
  background-color: rgb(33, 10, 241);
}
